@import "helpers/mixins";
@import "helpers/helpers";
@import "helpers/fonts";
@import "helpers/variables";


.services {
    background: $grey-green;
    padding-top: 90px;
    //padding-bottom: 76px
    padding-bottom: 16px;
    overflow: hidden;
    position: relative;
    @include desktop-less-lg {
        padding-top: 107px; }
    @include desktop-less-md {
        padding-bottom: 76px; }
    @include tablet-less-lg {
        padding-top: 114px;
        padding-bottom: 90px; }
    @include tablet-less-sm {
        padding-top: 110px; }
    @include mob-less-md {
        padding-top: 129px; }
    &__graphiс {
        position: relative;
        display: flex;
        margin-bottom: 37px;
        @include desktop-less-lg {
            margin-bottom: 48px; }
        @include desktop-less-md {
            margin-bottom: 40px; }
        @include desktop-less-sm {
            margin-bottom: 38px; }
        &:before {
            content: "";
            width: 100%;
            height: 4px;
            background-color: #000;
            border-radius: 100px;
            position: absolute;
            bottom: 0;
            left: 0; }
        .icon {
            height: 137px;
            @include tablet-less-lg {
                height: 130px; }
            @include mob-less-md {
                height: 112px; } }
        .icon-wind1 {
            width: 98px;
            margin-right: 16px;
            @include mob-less-lg {
                display: none; } }
        .icon-wind2 {
            width: 67px;
            display: none;
            @include mob-less-lg {
                display: block; } }
        .icon-windkraft-2x {
            width: 130px;
            display: none;
            @include mob-less-lg {
                display: block; } }
        .icon-windkraft-3x {
            width: 190px;
            margin-right: 58px;
            @include mob-less-lg {
                display: none; } }
        .icon-pv-2x {
            width: 84px;
            display: none;
            @include mob-less-lg {
                display: block; } }
        .icon-pv-3x {
            width: 132px;
            @include mob-less-lg {
                display: none; } }
        .icon-sun {
            width: 66px; } }
    &__title {
        margin-bottom: 35px;
        @include desktop-less-md {
            margin-bottom: 34px; }
        @include desktop-less-sm {
            margin-bottom: 36px; }
        h3 {
            line-height: 1.04;
            @include desktop-less-lg {
                line-height: 1.1; }
            @include desktop-less-sm {
                line-height: 1.05; }
            @include tablet-less-lg {
                font-size: 42px; }
            @include mob-less-lg {
                font-size: 30px;
                line-height: 1.07;
                margin-left: -2px; } } }


    &__list {
        display: flex;
        flex-wrap: wrap;
        @include desktop-less-md {
            display: block; } }
    &__item {
        //width: 100/3+%
        width:  33.333333%;
        margin-bottom: 30px;
        @include desktop-less-md {
            width: 100%;
            margin-bottom: -4px; }
        @include tablet-less-lg {
            margin-bottom: 4px; }
        @include tablet-less-sm {
            margin-bottom: -2px; }
        @include mob-less-lg {
            margin-bottom: -9px; } } }

.service {
    @include desktop-less-md {
        display: flex; }
    &__icon {
        width: 177px;
        height: 177px;
        position: relative;
        left: -33px;
        margin-bottom: 7px;
        @include desktop-less-lg {
            margin-bottom: 9px;
            width: 100%/12*2; }
        @include tablet-less-sm {
            width: 100%/14*3; }
        @include mob-less-lg {
            width: 100%/14*4; }
        img {
            width: 100%;
            display: block;
            @include desktop-less-lg {
                min-width: 177px; } } }
    &__in {
        @include desktop-less-md {
            width: 100%/12*9;
            padding-top: 67px; }
        @include tablet-less-lg {
            width: 100%/12*10; }
        @include tablet-less-sm {
            width: 100%/14*11; }
        @include mob-less-lg {
            width: 100%/14*10;
            padding-top: 47px; } }
    &__title {
        max-width: 85%;
        margin-bottom: 14px;
        @include desktop-less-lg {
            max-width: 85%;
            margin-bottom: 25px; }
        @include desktop-less-md {
            margin-bottom: 21px; }
        @include desktop-less-sm {
            margin-bottom: 19px; }
        @include tablet-less-lg {
            margin-bottom: 22px; }
        @include tablet-less-sm {
            margin-bottom: 21px; }
        @include mob-less-lg {
            max-width: none;
            margin-bottom: 18px;
            min-height: 48px;
            display: flex;
            align-items: flex-end; }
        @include mob-less-md {
            margin-bottom: 17px; }
        h6 {
            line-height: 1.08;
            @include tablet-less-lg {
                font-size: 22px; }
            @include mob-less-lg {
                font-size: 20px;
                line-height: 1.2; } } }
    &__text {
        max-width: 85%;
        font-size: 18px;
        line-height: 1.45;
        letter-spacing: -0.16px;
        @include desktop-less-lg {
            max-width: 90%; }
        @include mob-less-lg {
            font-size: 16px;
            line-height: 1.5; }
        a:not(.button) {
            color: inherit;
            transition: color .25s ease-in-out;
            cursor: pointer;
            text-decoration-color: $orange;
            @include hm-sb;
            &:hover {
                color: $orange; }
            &:active {
                color: $orange; } }
        ul {
            list-style-type: none;
            //max-width: 85%
            // max-width: 90%
            //+desktop-less-lg
 }            //    max-width: 90%
        li {
            //font-size: 18px
            //line-height: 1.45
            //letter-spacing: -0.16px
            padding-left: 15px;
            position: relative;
            margin-bottom: 5px;
            //&:last-of-type
            //    margin-bottom:  0
            @include desktop-less-lg {
                padding-left: 12px; }
            @include desktop-less-md {
                padding-left: 18px; }
            @include tablet-less-lg {
                padding-left: 14px; }
            @include mob-less-lg {
                //font-size: 16px
                //line-height: 1.5
                padding-left: 12px; }
            &:before {
                content: "";
                display: block;
                width: 5px;
                height: 5px;
                border-radius: 50%;
                background-color: #000;
                position: absolute;
                top: 10px;
                left: 0;
                @include desktop-less-md {
                    left: 3px; }
                @include mob-less-lg {
                    left: 1px;
                    top: 9px;
                    width: 4px;
                    height: 4px; } } }
        b {
            @include hm-sb; } } }
